import request from '@/axios/index'

const baseurl = '/datasource'

export function fetchList(query) {
  for (const key in query) {
    if (query[key] == null || query[key] === '') {
      delete query[key]
    }
  }
  return request({
    url: baseurl + '/page',
    method: 'get',
    params: query,
  })
}

export function save(data) {
  return request({
    url: baseurl,
    method: 'post',
    data,
  })
}

export function del(id) {
  return request({
    url: baseurl + '/' + id,
    method: 'delete',
  })
}
